@import url(https://fonts.googleapis.com/css?family=Roboto:500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{
    width: 100%;
    box-sizing: border-box;
    height: 8vh;
    background-color: rgb(255, 0, 119);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    padding-left: 30px;
    color: white;
    overflow: hidden;
}
p{
    margin: 0;
    font-size: 15px;
}
.pushRight{
    margin-right: auto;
    background-color:rgb(255, 66, 154);
    font-family: 'Kaushan Script', cursive;
}
.pushRight p{
    font-size: 1.5em;
    letter-spacing: 1.5px;
}
.logo{
    padding-right: 10px;
}

.IconItem{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    height: 100%;
    cursor: pointer;
    transition: all 0.5s;
}
.IconItem:hover{
    background-color: rgb(255, 66, 154);
}
.md-18{
    font-size: 18px;
    padding-right: 10px;
}

@media screen and (max-width:500px) {
    nav{
        padding: 0;
    }
    p{
     font-size: 0px;
     padding: 0;
    }
   .md-18{
       font-size: 20px;
       padding: 0px !important;
    }
   .IconItem:hover{
    background-color: rgb(255, 0, 119);
    }
    .pushRight{
        font-size: 0.8em;
        background-color: rgb(255, 0, 119);
    }
    .logo{
        height: 40px;
        width: 40px;
    }
 }
.landingPage{
    height: 92vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Form{
    width: 350px;
    overflow: hidden;
    transition: all 0.5s;
}
.buffer:first-child{
    height: 12px;
    background-color: rgb(255, 0, 119);
    transform: translateX(175px);
    transition: all 1s;
}
.buffer:last-child{
    height: 12px;
    background-color: rgb(255, 0, 119);
    transform: translateX(-175px);
    transition: all 1s;
}
.Form:hover .buffer:first-child{
    transform: translateX(0);
}
.Form:hover .buffer:last-child{
    transform: translateX(0);
}
.info{
    background-color: rgb(255, 223, 236);
    transition: all 1s;
    overflow: hidden;
}
.login{
    display: flex;
    justify-content: center;
    align-items:center;
    height: 50px;
    font-size: 1.5em;
    color: rgb(255, 0, 119);
    padding-top:55px;
    padding-bottom: 7px;
    margin-top: 30px;
    margin-bottom: 30px;
}
input{
    text-align: center;
    color:rgb(255, 0, 119)
}
.input{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 170px;
}
.pad{
    margin-top: 10px;
}                               
.submit{
    margin-bottom: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.info:hover{
    background-color: rgb(255, 244, 249);
}

@media screen and (max-width: 500px) {
    .Form {
      width: 290px;
    }
